.about-menu__modal-box {

    width: 100%;
    min-height: 502px;
    background-color: var(--second-title-color);
    border-radius: 4px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
    transform: translateX(0);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    padding: 40px 40px; 

    @media screen and (min-width: 480px) {
        width: 450px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        }

    @media screen and (min-width: 1200px) {
        width: 528px;
    }
}

.about-menu__button-close {
    @include flex($df: flex, $jcc: center, $aic: center);
    padding: 0;
    box-sizing: border-box;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 8px;
    top: 8px;
    background: transparent;
    border: 1px solid #d41443;
    border-radius: 50%;
    cursor: pointer;
}

.about-menu__icon {
    fill: var(--accent-color);
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    
    &:hover {
    fill: var(--hover-main-btn-color);
    }
}

.modal__img {
    margin: 0px auto 30px auto;
}

.about-menu__text {
    font-family: DM Sans;
    font-style: normal;
    @include font($fs: 12px, $fw: 400, $lh: 1.66, $ls: 0.02em);
    color: var(--fourth-text-color);
    text-align: center;
    margin-bottom: 30px;

    @media screen and (min-width: 768px) {
        @include font($fs: 16px, $fw: 500, $lh: 1.66, $ls: 0.02em);  
    }

    @media screen and (min-width: 1200px) {
        @include font($fs: 18px, $fw: 500, $lh: 1.66, $ls: 0.02em);  
    }
}

.about-menu__social {
    width: 100%;
}

.about-menu__social-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}

.about-menu__social-item {
    &:not(:last-child) {
    margin-right: 30px;
    }
}

.about-menu__social-link {
    @include flex($df: flex, $jcc: center, $aic: center);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color: var(--second-title-color);
    background-color: var(--social-icon-color);

    transition: background-color cubic-bezier(0.4, 0, 0.2, 1) 250ms;

    &:hover,
    &:focus {
    background-color: var(--hover-social-icon-color);
    color: var(--second-title-color);
    }
}
